import { onMounted, ref, computed, watch } from "vue";
import { useRouter } from "vue-router";
import { MessageBox, Setting, User, Service, View as IcView, Help, Check, OfficeBuilding } from "@element-plus/icons-vue";
export default {
  components: {
    MessageBox: MessageBox,
    Setting: Setting,
    User: User,
    Service: Service,
    IcView: IcView,
    Help: Help,
    Check: Check,
    OfficeBuilding: OfficeBuilding
  },
  props: {
    modelValue: {
      type: Boolean,
      required: true
    }
  },
  setup: function setup() {
    var router = useRouter();
    var activeMenu = ref("");
    var currentRoute = computed(function () {
      return router.currentRoute.value.path + router.currentRoute.value.hash;
    });
    watch(currentRoute, function () {
      activeMenu.value = currentRoute.value;
    });

    var handleSelect = function handleSelect(key) {
      router.push(key);
    };

    var size = ref("20%");

    var handleSize = function handleSize() {
      if (window.innerWidth <= 600) {
        size.value = "100%";
      } else if (window.innerWidth <= 850) {
        size.value = "40%";
      } else if (window.innerWidth <= 1100) {
        size.value = "30%";
      } else {
        size.value = "20%";
      }
    };

    onMounted(function () {
      window.addEventListener("resize", handleSize);
      handleSize();
      activeMenu.value = router.currentRoute.value.fullPath;
    });
    return {
      handleSelect: handleSelect,
      size: size,
      activeMenu: activeMenu
    };
  }
};